import { Component, Vue } from 'vue-property-decorator';
import { ContactService, ContactModel, CoreService } from '@/sdk';
import UndoDeleteComponent from './undo/undo-delete.component';
import ViewContactComponent from './view/view-contact.componen';

@Component({
  components: {
    ViewContactComponent
  }
})
export default class AllContactsView extends Vue {
  // Properties
  private Contacts: Array<ContactModel> = [];
  public DeleteDialogMode = false;

  // Computed Properties
  get FilteredContacts() {
    return this.Contacts.filter(
      contact =>
        `${contact.FirstName} ${contact.LastName}`
          .toLowerCase()
          .includes(this.ContactService.Search.toLowerCase()) ||
        !contact.FirstName
    );
  }

  // Services
  private ContactService = ContactService.Instance;

  // Methods
  public created() {
    this.ContactService.Contacts.subscribe(Res => {
      this.Contacts = Res;
    });
  }
  public changeActiveLink() {
    CoreService.Instance.ActiveLink = 'add-contact';
  }
  public deleteContact() {
    this.DeleteDialogMode = false;
    const loading = (this as any).$vs.loading({
      background: 'dark',
      color: 'warning'
    });
    setTimeout(() => {
      this.ContactService.deleteContact();
      loading.close();
      (this as any).$vs.notification({
        title: 'Deleted',
        text: 'Contact has been deleted successfully.',
        color: 'dark',
        icon: `<i class='bx bx-bell' />`,
        progress: 'auto',
        flat: true,
        clickClose: true,
        content: UndoDeleteComponent
      });
    }, 3000);
  }
}
