import { ContactModel, Stack } from '../models';
import { SortService } from './sort.service';
import { CoreService } from './core.service';
import VueRouter from 'vue-router';
import { BehaviorSubject } from 'rxjs';

export class ContactService {
  private static _Instance: ContactService;
  static get Instance() {
    if (!ContactService._Instance) {
      ContactService._Instance = new ContactService();
    }
    return ContactService._Instance;
  }
  private constructor() {
    if (!localStorage.getItem('contacts')) {
      localStorage.setItem('contacts', JSON.stringify([]));
    }
    this.Contacts.next(JSON.parse(localStorage.getItem('contacts') ?? '[]'));
    this.refreshContacts();
  }

  // Services
  private SortService = new SortService<ContactModel>();

  // Contacts
  public Search = '';
  public Contacts = new BehaviorSubject<Array<ContactModel>>([]);
  public DeletedContactId = '';
  public ViewContactMode = false;
  public ViewContact = new ContactModel();

  // Temp contacts
  public TempContactsStack = new Stack<ContactModel>();

  // Save Contact
  public SaveContactData = new ContactModel();

  // Methods
  public deleteContact() {
    const index = this.Contacts.value.findIndex(
      contact => contact.Id === this.DeletedContactId
    );
    if (index > -1) {
      this.TempContactsStack.push(this.Contacts.value[index]);
      this.Contacts.value.splice(index, 1);
      localStorage.setItem(
        'contacts',
        JSON.stringify(
          this.Contacts.value.filter(
            contact =>
              !!contact.FirstName && !!contact.LastName && !!contact.Contact
          )
        )
      );
      this.Contacts.next(JSON.parse(localStorage.getItem('contacts') ?? '[]'));
      this.sortContacts();
      this.addLetterHeads();
    }
  }

  public saveContact($router: VueRouter) {
    if (!this.SaveContactData.Id) {
      this.SaveContactData.Id =
        Math.random()
          .toString(36)
          .substring(2) + Date.now().toString(36);
      this.Contacts.value.push(this.SaveContactData);
    } else {
      const index = this.Contacts.value.findIndex(
        contact => contact.Id === this.SaveContactData.Id
      );
      if (index > -1) {
        this.Contacts.value[index] = { ...this.SaveContactData };
      }
    }
    this.refreshContacts();
    $router.push({ name: 'All Contacts' });
    CoreService.Instance.ActiveLink = 'all-contacts';
    localStorage.setItem(
      'contacts',
      JSON.stringify(
        this.Contacts.value.filter(
          contact =>
            !!contact.FirstName && !!contact.LastName && !!contact.Contact
        )
      )
    );
  }

  private sortContacts() {
    const comparator = (a: ContactModel, b: ContactModel) => {
      if (a.FirstName!.toLowerCase() < b.FirstName!.toLowerCase()) {
        return -1;
      } else if (a.FirstName!.toLowerCase() === b.FirstName!.toLowerCase()) {
        return 0;
      }
      return 1;
    };
    this.Contacts.next(this.SortService.sort(this.Contacts.value, comparator));
  }

  private addLetterHeads() {
    const final: Array<ContactModel> = [];
    let lastChar: string | null = null;
    this.Contacts.value.forEach(contact => {
      const firstLetter = contact.FirstName!.substring(0, 1).toUpperCase();
      if (!!firstLetter) {
        if (firstLetter !== lastChar) {
          final.push(new ContactModel({ GroupLetter: firstLetter }));
          lastChar = firstLetter;
        }
        final.push(contact);
      }
    });
    this.Contacts.next([...final]);
  }

  public refreshContacts() {
    this.sortContacts();
    this.addLetterHeads();
    localStorage.setItem(
      'contacts',
      JSON.stringify(
        this.Contacts.value.filter(
          contact =>
            !!contact.FirstName && !!contact.LastName && !!contact.Contact
        )
      )
    );
  }
}
