export class SortService<T = number> {
  private comparator!: (a: T, b: T) => number;
  private swap(arr: Array<T>, leftIndex: number, rightIndex: number) {
    const temp = arr[leftIndex];
    arr[leftIndex] = arr[rightIndex];
    arr[rightIndex] = temp;
  }
  private partition(arr: Array<T>, left: number, right: number) {
    const pivot = arr[Math.floor((right + left) / 2)];
    let i = left;
    let j = right;

    while (i <= j) {
      while (this.comparator(arr[i], pivot) === -1) {
        i++;
      }
      while (this.comparator(arr[j], pivot) === 1) {
        j--;
      }
      if (i <= j) {
        this.swap(arr, i, j);
        i++;
        j--;
      }
    }
    return i;
  }
  private quickSort(arr: Array<T>, left: number, right: number) {
    let index: number;
    if (arr.length > 1) {
      index = this.partition(arr, left, right);
      if (left < index - 1) {
        this.quickSort(arr, left, index - 1);
      }
      if (index < right) {
        this.quickSort(arr, index, right);
      }
    }
    return arr;
  }
  public sort(arr: Array<T>, comparator?: (a: T, b: T) => number) {
    if (!comparator) {
      comparator = (a: T, b: T) => {
        if (a < b) {
          return -1;
        } else if (a === b) {
          return 0;
        }
        return 1;
      };
    }
    this.comparator = comparator;
    return this.quickSort(arr, 0, arr.length - 1);
  }
}
