import { Component, Vue, Prop } from 'vue-property-decorator';
import { ContactService, ContactModel } from '@/sdk';

@Component
export default class ViewContactComponent extends Vue {
  // Props
  @Prop({
    default: () => new ContactModel(),
    type: Object
  })
  private readonly contact!: ContactModel;

  // Computed Properties
  get Contact() {
    return this.ContactService.ViewContact;
  }

  // Services
  private ContactService = ContactService.Instance;
}
