import { Component, Vue } from 'vue-property-decorator';
import { ContactService, ContactModel } from '@/sdk';

@Component
export default class RecentContactsView extends Vue {
  // Services
  private ContactService = ContactService.Instance;

  // Methods
  public restore(contact: ContactModel) {
    const loading = (this as any).$vs.loading({
      background: 'dark',
      color: 'warning'
    });
    setTimeout(() => {
      const restoredContact = this.ContactService.TempContactsStack.remove(
        this.ContactService.TempContactsStack.Items.findIndex(
          item => contact.Id === item.Id
        )
      );
      this.ContactService.Contacts.next([
        ...this.ContactService.Contacts.value,
        restoredContact
      ]);
      this.ContactService.refreshContacts();
      loading.close();
      (this as any).$vs.notification({
        title: 'Restored',
        text: 'Contact has been restored successfully.',
        color: 'dark',
        icon: `<i class='bx bx-bell' />`,
        progress: 'auto',
        flat: true
      });
    }, 3000);
  }
  public restoreAll() {
    const loading = (this as any).$vs.loading({
      background: 'dark',
      color: 'warning'
    });
    setTimeout(() => {
      this.ContactService.Contacts.next([
        ...this.ContactService.Contacts.value,
        ...this.ContactService.TempContactsStack.removeAll()
      ]);
      this.ContactService.refreshContacts();
      loading.close();
      (this as any).$vs.notification({
        title: 'Restored',
        text: 'All contacts have been restored successfully.',
        color: 'dark',
        icon: `<i class='bx bx-bell' />`,
        progress: 'auto',
        flat: true
      });
    }, 3000);
  }
}
