import { Component, Vue } from 'vue-property-decorator';
import { ContactService } from '@/sdk';

@Component
export default class UndoDeleteComponent extends Vue {
  // Services
  private ContactService = ContactService.Instance;

  // Methods
  public undo() {
    const contact = this.ContactService.TempContactsStack.pop();
    if (typeof contact === 'object') {
      this.ContactService.DeletedContactId = '';
      this.ContactService.Contacts.next([
        ...this.ContactService.Contacts.value,
        contact
      ]);
    }
    this.ContactService.refreshContacts();
  }
}
