import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import AllContactsView from '@/views/contacts/all/all-contacts.view';
import SaveContactsView from '@/views/contacts/save/save-contact.view';
import RecentContactsView from '@/views/contacts/recent/recent-contacts.view';

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: '/',
    name: 'All Contacts',
    component: AllContactsView
  },
  {
    path: '/recent-contacts',
    name: 'Recently Deleted Contacts',
    component: RecentContactsView
  },
  {
    path: '/add-contact',
    name: 'Add Contact',
    component: SaveContactsView
  },
  {
    path: '/edit-contact/:Id',
    name: 'Edit Contact',
    component: SaveContactsView
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;
