import { Component, Vue } from 'vue-property-decorator';
import { ContactService, ContactModel } from '@/sdk';

@Component
export default class SaveContactsView extends Vue {
  // Services
  private ContactService = ContactService.Instance;

  // Methods
  public created() {
    const { Id } = this.$route.params;
    if (!!Id) {
      this.ContactService.SaveContactData = this.ContactService.Contacts.value.find(
        contact => contact.Id === Id
      )!;
    } else {
      this.ContactService.SaveContactData = new ContactModel();
    }
  }

  public saveContact() {
    const loading = (this as any).$vs.loading({
      background: 'dark',
      color: 'warning'
    });
    setTimeout(() => {
      const { Id } = this.ContactService.SaveContactData;
      this.ContactService.saveContact(this.$router);
      loading.close();
      (this as any).$vs.notification({
        title: Id ? 'Updated' : 'Added',
        text: `Contact has been ${Id ? 'updated' : 'added'} successfully.`,
        color: 'dark',
        icon: `<i class='bx bx-bell' />`,
        progress: 'auto',
        flat: true
      });
    }, 3000);
  }
}
